// button
$btn-height: 35px;
$btn-padding: 10px;
$btn-margin-y: 2px;
$btn-border-radius: 2px;

// form
$form-height: $btn-height;
$form-padding: $btn-padding;
$form-margin-y: $btn-margin-y;
$form-border-radius: $btn-border-radius;

// color
$color-background: #fff;
$color-font: #333;

$color-primary: #FF4533;
$color-font-primary: #fff;
$color-secondary: #FF7C4C;
$color-font-secondary: #A3421F;
$color-accent: #00A875;
$color-font-accent: #fff;

$color-success: mix(#28b515, $color-primary, 99%);
$color-font-success: darken($color-success, 20);
$color-warning: mix(#ffd800, $color-primary, 99%);
$color-font-warning: darken($color-warning, 25);
$color-error: mix(#ff4949, $color-primary, 99%);
$color-font-error: darken($color-error, 35);
$color-info: mix(#0897e2, $color-primary, 99%);
$color-font-info: darken($color-info, 25);

$color-lightgray: mix(#eee, $color-primary, 99%);
$color-font-lightgray: darken($color-lightgray, 33);
$color-midgray: mix(#aaa, $color-primary, 99%);
$color-font-midgray: darken($color-midgray, 33);
$color-darkgray: mix(#555, $color-primary, 99%);
$color-font-darkgray: lighten($color-darkgray, 66);

$color-highlight: mix(#fd0, $color-primary, 99%);

// fonts
$font-family: sans-serif;
$font-line: 1.6em;
$font-size: 16px;
$font-space: 0.01em;
$font-weight: 300;

$font-title-family: Georgia, serif;
$font-title-line: 1em;
$font-title-space: 0;
$font-title-weight: 400;
$font-title-style: normal;
$font-title-line-weight: 2px;
$font-title-line-width: 3vw;

$font-code-family: monospace;
$font-code-space: 0;
$font-code-weight: 400;
$font-code-radius: $btn-border-radius;

$font-link-family: $font-title-family;
$font-link-line: $font-line;
$font-link-size: $font-size;
$font-link-space: $font-title-space;
$font-link-weight: $font-title-weight;

$font-form-family: $font-family;
$font-form-line: $font-line;
$font-form-size: $font-size * .77;
$font-form-space: $font-space;
$font-form-weight: $font-weight;

// margins
$layout-space: 7vw;
$element-space: 24px;
